import { Ville } from '../../admin/ville/metier/ville.metier';

export class Client {
  id: number;
  nom: string = '';
  proprietaire: string = '';
  adresse: string = '';
  typeClient: string = '';
  etatClient: string = null;
  ville: Ville;
  commentaire: string = '';
  nombreAppareils: Number = 0;
  dateEngagement: Date = null;
  email: string = '';
  photo: string = '';

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



