import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Client } from "../metier/client.metier";
import { ClientService } from "../service/client.service";
import { BaseComponent } from "../../core/component/base-component";
import { Params, ActivatedRoute } from "@angular/router";
import { Ville } from "../../admin/ville/metier/ville.metier";
import { VilleService } from "../../admin/ville/service/ville.service";
import { CustomValidators } from 'ng2-validation';
import { Response } from '@angular/http';

@Component({
  selector: 'ms-client-edit',
  templateUrl: './client.edit.component.html',
  styleUrls: ['./client.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ClientEditComponent extends BaseComponent implements OnInit {

  private client: Client;
  public clientForm: FormGroup;
  private typeClientList: string[] = [];
  private etatClientList: string[] = [];
  private villeList: Array<Ville> = [];
  private uploadFile: any;
 

  constructor(private fb: FormBuilder,
    private route: ActivatedRoute,
    private pageTitleService: PageTitleService,
    private clientService: ClientService,
    private villeService: VilleService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("MODIFIER UN CLIENT");
    this.loadClient();
    this.clientForm = this.fb.group({
      id: [Validators.required],
      nom: ['', Validators.required],
      adresse: ['', Validators.required],
      typeClient: ['', Validators.required],
      etatClient: ['', Validators.required],
      ville: ['', Validators.required],
      proprietaire: '',
      commentaire: '',
      nombreAppareils: null,
      dateEngagement: null,
      email: ['', Validators.email],
    });
  }

  updateClient(clientForm: any) {
    this.client = new Client(clientForm);
    
    this.client.photo = this.photo;
    if (this.client.photo === this.DEFAULT_PHOTO) {
      this.client.photo = null;
    }

    this.clientService.saveClient(this.client)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/client/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  loadClient() {
    this.route.params.switchMap((params: Params) => this.clientService.getClient(+params['id']))
      .subscribe(client => {
        this.client = client;
        this.clientForm.patchValue(this.client);
        this.photo = client.photo;

        if (this.photo == null) {
          this.photo = this.DEFAULT_PHOTO;
        }

        this.loadTypeClientList();
        this.loadEtatClientList();
        this.loadVilleList();

      }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteClient(clientForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.client = new Client(clientForm);
    this.clientService.deleteClient(this.client)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/client/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadTypeClientList() {
    this.clientService.getTypeClientList()
      .subscribe(typeClientList => {
        this.typeClientList = typeClientList;
        this.clientForm.patchValue({ typeClient: this.client.typeClient }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      );
  }

  private loadEtatClientList() {
    this.clientService.getEtatClientList()
      .subscribe(etatClientList => {
        this.etatClientList = etatClientList;
        this.clientForm.patchValue({ etatClient: this.client.etatClient }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      );
  }

  private loadVilleList() {
    this.villeService.getVilleList()
      .subscribe(villeList => {
        this.villeList = villeList;
        this.client.ville = this.villeList.find(ville => ville.id === this.client.ville.id);
        this.clientForm.patchValue({ ville: this.client.ville }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  isValide() {
    if (this.clientForm.get('etatClient').value === 'VALIDE') {
      return true;
    } else {
      return false;
    }
  }

  checkCommentaire() {
    if (this.clientForm.get('etatClient').value != 'VALIDE') {
      if (this.clientForm.get('commentaire').value === null || this.clientForm.get('commentaire').value === '') {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  /*    Save and get the photo into vito-back    */

  /*
   upload() {
     this.currentFileUpload = this.selectedFiles.item(0)
     this.clientService.savePhoto(this.currentFileUpload).subscribe(event => {
       if (event instanceof Response) {
         console.log('File is completely uploaded!');
       }
     })
 
     this.selectedFiles = undefined
   }
   */

  /*
   private getClientPhoto() {
     this.clientService.getClientPhoto()
       .subscribe(photo => {
         this.photo = 'data:image/png;base64,' + photo;
         // console.log("photo : "+photo);
       }, error => this.showError(error.status, error.json().message)
       )
       ;
   }
 */

}






