"use strict";

import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Client } from "../metier/client.metier";
import { FormGroup, FormBuilder } from "@angular/forms";
import { BaseComponent } from "../../core/component/base-component";
import { ClientService } from "../service/client.service";
import { Ville } from "../../admin/ville/metier/ville.metier";
import { VilleService } from "../../admin/ville/service/ville.service";
import { PageTitleService } from "../../core/page-title/page-title.service";

@Component({
  selector: 'ms-client-list',
  templateUrl: './client.list.component.html',
  styleUrls: ['./client.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ClientListComponent extends BaseComponent implements OnInit {

  public clientForm: FormGroup;
  private client: Client;
  private clientList: Array<Client> = [];
  private typeClientList: string[] = [];
  private etatClientList: string[] = [];
  private villeList: Array<Ville> = [];

  constructor(private fb: FormBuilder,
    private clientService: ClientService,
    private villeService: VilleService,
    private pageTitleService: PageTitleService, ) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES CLIENTS");
    this.loadClientList();
    this.loadTypeClientList();
    this.loadVilleList();
  }

  createForm() {
    this.clientForm = this.fb.group({
      nom: '',
      proprietaire: '',
      typeClient: '',
      ville: null,
    });
  }


  findClientList(clientForm: any) {
    this.client = new Client(clientForm);
    this.clientService.findClientListByCriteria(this.client)
      .subscribe(clientList => {
        this.clientList = clientList;
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadClientList() {
    this.clientService.getClientList()
      .subscribe(clientList => {
        this.clientList = clientList;
        for (let client of this.clientList) {
          if (client.photo === null) {
            client.photo = this.DEFAULT_PHOTO;
          }
        }
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadTypeClientList() {
    this.clientService.getTypeClientList()
      .subscribe(typeClientList => {
        this.typeClientList = typeClientList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadEtatClientList() {
    this.clientService.getEtatClientList()
      .subscribe(etatClientList => {
        this.etatClientList = etatClientList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadVilleList() {
    this.villeService.getVilleList()
      .subscribe(villeList => {
        this.villeList = villeList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

}






