import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Client } from "../metier/client.metier";
import { ClientService } from "../service/client.service";
import { BaseComponent } from "../../core/component/base-component";
import { Ville } from "../../admin/ville/metier/ville.metier";
import { VilleService } from "../../admin/ville/service/ville.service";

@Component({
  selector: 'ms-client-add',
  templateUrl: './client.add.component.html',
  styleUrls: ['./client.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ClientAddComponent extends BaseComponent implements OnInit {

  public clientForm: FormGroup;
  private client: Client;
  private typeClientList: string[] = [];
  private villeList: Array<Ville> = [];

  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService, private clientService: ClientService,
    private villeService: VilleService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UN CLIENT");
    this.clientForm = this.fb.group({
      nom: ['', Validators.required],
      adresse: ['', Validators.required],
      typeClient: ['', Validators.required],
      ville: ['', Validators.required],
      etatClient: 'VALIDE',
      proprietaire: '',
      nombreAppareils: null,
      dateEngagement: null,
      email: ['', Validators.email],
    });

    this.loadTypeClientList();
    this.loadVilleList();
  }

  saveClient(clientForm: any) {
    this.client = new Client(clientForm);

    this.client.photo = this.photo;
    if (this.client.photo === this.DEFAULT_PHOTO) {
      this.client.photo = null;
    }

    this.clientService.saveClient(this.client)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(["/client/list"]);
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadTypeClientList() {
    this.clientService.getTypeClientList()
      .subscribe(typeClientList => {
        this.typeClientList = typeClientList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadVilleList() {
    this.villeService.getVilleList()
      .subscribe(villeList => {
        this.villeList = villeList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  reset() {
    this.clientForm.reset();
  }

}






